MeteorVersion = {
  getSemanticVersion: function () {
    var meteorVersion = Meteor.release
    if (meteorVersion) {
      var atIndex = meteorVersion.indexOf('@');
      if (atIndex !== -1) {
        meteorVersion = meteorVersion.substr(atIndex + 1);
      }
      var mainVersionRegEx = /^\d+\.\d+\.\d+/;
      var mainVersionMatch = mainVersionRegEx.exec(meteorVersion);
      meteorVersion = mainVersionMatch ? mainVersionMatch[0] : undefined;
    }

    return meteorVersion;
  }
}
